img {
  max-width: 45%;
  height: 100%;
  display: block;
}

.cat-container {
  flex-direction: column;
  gap: 20px;
  padding: 30px;
  display: flex;
}

.ss-main {
  width: 15%;
  margin-bottom: 10px;
}

.cat-details {
  flex-direction: column;
  display: flex;
}

.cat-info {
  max-width: 80%;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  display: flex;
}

.cat-name {
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
  font-size: 30px;
}

.cat-text {
  font-size: 15px;
}

.error, .loader {
  display: none;
}

.error {
  color: red;
}

.loader {
  color: #000;
  max-width: 15%;
  padding: 20px;
  font-size: 16px;
  font-weight: 400;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "Loading data, please wait...";
  z-index: 5;
  position: relative;
}

.loader:before {
  content: "";
  height: 6px;
  width: 80%;
  background: linear-gradient(#000 100%, rgba(0, 0, 0, 0) 0) 0 0 / 0% no-repeat;
  border: 1px solid;
  border-radius: 10px;
  animation: lineGrow 1s linear infinite;
  position: absolute;
  bottom: 9px;
}

@keyframes lineGrow {
  to {
    background-size: 100%;
  }
}

